import styled from '@emotion/styled';

let fullWidth = parseInt(window.innerWidth);
if (fullWidth > 780) fullWidth = 640;
const ww86 = window.innerWidth < 360 ? fullWidth : parseInt(fullWidth * 0.86);

const W85 = `max-width: ${ww86}px; width:${ww86}px`;
const FW = `max-width: ${fullWidth}px; width:${fullWidth}px`;

export const DivW85 = styled.div`
    ${W85}
`;

export const PW85 = styled.p`
    ${W85}
`;

export const DivW = styled.div`
    ${FW}
`;
export const TableW = styled.table`
    ${FW}
`;

export const bps = {
    //@media screen and (min-width: 960px) {
    secreen960: '@media screen and (min-width: 960px)'
  }

export const MenuIz = styled.div`
    background-color: #fff;
    line-height: 15px;
    width: 100%;
    color: #535353;
`;

export const MenuIzTable = styled.table`
    width: 100%;

    tr {
        cursor: pointer;

        &.bo {
            border-bottom: 1px solid #cccccc;
        }
        &.nocursor {
            cursor: default;
        }
        &.nocursor:hover {
            background-color: transparent;
        }
        &.active,
        :hover {
            background-color: #e5e5e5;
        }

        td {
            height: 68px;

            :nth-of-type(1) {
                width: 53px;
                text-align: center;
            }
            :nth-of-type(2) {
                padding-right: 12px;
            }

            a {
                display: block;
                width: 100%;
                height: 68px;
                line-height: 68px;
                padding-left: 27px;
                text-decoration: none;
                color: #535353;
            }

            &.sim {
                text-align: left !important;
                padding-left: 27px;
            }
            &.nopad {
                padding: 0;
            }
            &.timen {
                letter-spacing: 4px !important;
                font-size: 12px !important;
                padding-left: 21px !important;
                color: #999 !important;
                height: 40px;
                background-color: #eeeeee;
            }
        }
    }
`;

export const MenuIzWrap = styled.div`
    height: 100%;
    overflow-y: auto;
}`;

export const MenuIzHead = styled.div`
    width: 100%;
    background-color: #00aae4;
    color: #fff;
    font-size: 15px;
    padding: 13px;

    p {
        margin: 9px 0 5px 0;
        line-height: 26px;
    }
}`;

export const MenuIzIndice = styled.ul`
    list-style: none;
    padding: 0;
    margin: 0;
    background-color: #eeeeee;
`;
const getItemIndiceStyles = ({styleParams}) => {
    const {activo, visto, n} = styleParams;

    let outStyle = '';

    switch (n) {
        case 1:
            outStyle += `font-weight: 900;
            border-bottom: 1px solid #c8c8c8;
            border-top: 1px solid #c8c8c8;`;
            break;
        case 2:
            outStyle += `position: relative;
            text-indent: 21px;

            :after {
                content: '';
                background-color: #009ddd;
                width: 13px;
                height: 2px;
                position: absolute;
                left: 30px;
                top: 21px;
            }`;
            break;
        case 3:
            outStyle += `position: relative;
            text-indent: 42px;
            
            :after {
                content: '';
                background-color: #ff6859;
                width: 13px;
                height: 2px;
                position: absolute;
                left: 54px;
                top: 21px;
            }`;
            break;

        default:
            break;
    }
    if (activo) {
        outStyle += `background-color: #e5e5e5;`;
    } else {
        outStyle += `cursor: pointer;
                    :hover {
                        background-color: #e5e5e5;
                    }`;
    }
    if (visto) {
        outStyle += `/* check.svg */
        background-image: url(data:image/svg+xml;base64,);`;
    } else {
        outStyle += `/* item.svg */
        background-image: url(data:image/svg+xml;base64,);`;
    }
    return outStyle;
};

export const MenuIzIndiceItem = styled.li`
    display: block;
    width: 100%;
    padding: 12px 10px 12px 36px;
    color: #535353;
    margin-bottom: -1px;
    background-repeat: no-repeat;
    background-position: right center;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;

    ${getItemIndiceStyles}

`;

const getImageDisabled = props => {
    if(props.styleLoading){
        return `opacity: 0.4;`
    }else{
        return `opacity: 1`
    }

}

const getBgDisabled = props => {
    if(props.styleLoading){
        return `background-color: rgb(0, 170, 228, 0.8);
        background-image: none !important;
        cursor: default;`
    }else{
        return `background-color: #00aae4;
        background-image: url(data:image/svg+xml;base64,);
        cursor: pointer;`
    }

}

export const HeaderLogo = styled.div`
    
    text-align: center;
    padding-left: 10px;

    background-repeat: no-repeat;
    background-position: 0 20px;
    width: 58px;
    height: 52px;
    position: absolute;

    ${getBgDisabled}

    img{
        margin-top: 20px;

        &.ieslogo {
            max-width: 32px;
            margin-top: 21px;
        }
        ${getImageDisabled}
    }
    
`

const getPaddingH1 = props => {
    const {showPreguntasBtn, showUtilsBtn} = props.styleParams;
    if (showPreguntasBtn && showUtilsBtn) {
        return 'padding-right:140px';
    } else if (showPreguntasBtn || showUtilsBtn) {
        return 'padding-right:90px';
    } else {
        return 'padding-right:60px';
    }
};

export const HeaderCont = styled.header`

    background-color: #009ddd;
	width: 100%;
	position: fixed;
	height: 52px;
	top: 0;
    z-index: 44;
    
    background-color: #fff;
    border-bottom: 1px solid #ccc;

    h1 {
        color: #535353;
        text-align: left;
        font-weight: 400;
        margin: 0;
        height: 52px;
        line-height: 52px;
        font-size: 16px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        padding-left: 68px;
        ${getPaddingH1}
    }

    
`
